import { City, Park, Tour, User, Itinerary, DashboardStats } from '@/types';
import { 
  demoCities, 
  demoParks, 
  demoTours, 
  demoUsers, 
  demoItineraries, 
  demoStats 
} from './demoData';

// Simular delay de rede
const delay = (ms: number) => new Promise(resolve => setTimeout(resolve, ms));

// Cidades
export const demoCitiesService = {
  async getAll(): Promise<City[]> {
    await delay(500);
    return [...demoCities];
  },

  async getById(id: string): Promise<City | null> {
    await delay(300);
    return demoCities.find(city => city.id === id) || null;
  },

  async create(city: Omit<City, 'id' | 'createdAt'>): Promise<string> {
    await delay(800);
    const newCity: City = {
      ...city,
      id: Date.now().toString(),
      createdAt: new Date()
    };
    demoCities.push(newCity);
    return newCity.id;
  },

  async update(id: string, city: Partial<City>): Promise<void> {
    await delay(600);
    const index = demoCities.findIndex(c => c.id === id);
    if (index !== -1) {
      demoCities[index] = { ...demoCities[index], ...city };
    }
  },

  async delete(id: string): Promise<void> {
    await delay(400);
    const index = demoCities.findIndex(c => c.id === id);
    if (index !== -1) {
      demoCities.splice(index, 1);
    }
  }
};

